*{
    font-family: 'Quattrocento Sans', sans-serif;
    text-decoration:none;
  }
  
  body{
    margin: 0;
    background-color: #ecf0f5;
  }
  
  
  
  
  
  .sidenav{
    height: 100%;
    width: 160px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #222d32;
    overflow-x: hidden;
    padding-top: 20px;
  }
  
  .logo{
    width: 70px;
    height: 60px;
    margin-left: 30px;
    padding: 20px;
  }
  
  .tg1{
    
  
  
      padding: 6px 4px 6px 9px;
      margin-top: 10px; 
      margin-left: 10px;
      margin-right: 10px;
      margin-bottom: 10px;
      color:#ecf0f5;
      text-align: center;
      text-decoration: none;
      font-size: 20px;
      display: block;
    
  }
  
  
  
  
  
  .sidenav a:hover{
    color: #ff5e5b;
    
  }
  
  
  .icons{
      
      padding: 10px;
  
  }
  .dp{
    width:35px;
    margin-bottom: 2px;
    
    
  }
  .topbar{
    width:auto;
    overflow: hidden;
    height:50px;
    background-color: #77cc6d;
  }
  
  
  
  
  
  .nav-right{
    
    
    
    float: right;
    margin-right: 35px;
    
  
  }
  
  .main{
    width:auto;
    margin-left: 200px;
    margin-top: 50px;
    
    margin-bottom: 50px;
    display: grid;
    
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: minmax(50px, auto);
    grid-gap: 3em;
    
  }
  .main > div{
    background: #ecf0f5;
    
  } 
  
  .box{
    border-radius: 4px;
  }
  
  .box1{
   grid-column: 1/2;
  
  } 
  
  .box2{
   grid-row: 2/3;
  
  } 
  
  .box3{
    grid-column: 1/3;
    grid-row: 3/4;
  } 
  
  .box4{
  grid-row: 4/4;
  grid-column-start: 1;
  grid-column-end: 4;
  } 
  
  .box5{  
  background-color: white;
  grid-column: 2/4;
  grid-row: 1/2;
  margin-bottom: -130px;
  } 
  
  .box6{
    grid-column: 2/3;
    margin-top: 130px;
  } 
  
  .box7{
    margin-top: 130px;
    grid-column: 3/4;
    grid-row-start:2;
    grid-row-end: 4;
    align-self: stretch;
  } 
  
  /*search-box-start*/
  
  .search-container{
    margin-left: 300px;
    margin-top: -27px;
    
    
  }
  .search-box{
    outline: none;
    font-size: 18px;
    border-style: thin; 
    
    padding-right:10px;
    
    border-radius: 6px;
    padding: 9px;
  }
  
  .search-icon{
    margin-bottom: -7px;
    padding-left: 20px;
    cursor: pointer;
    
    
  }
  .grid-container{
    overflow: hidden;
    margin-right:20px;
  }
  /* search-box-end*/
  
  
  
  
  
  
  /* responsive code*/
  
  @media only screen and (max-width: 800px){
  
    .search-container{
      
      margin-left:80px;
      margin-top: -38px;
    }
    .search-box{
      width: 80px;
    }
  
  .sidenav{
    display:none;
  }
  .tg1{
    display:none;
  }
    .sidenav22{
      height: 10%;
      width: 100%;
      position: relative;
      
      top: 0;
      left: 0;
      background-color: #222d32;
      overflow-x: hidden;
      padding-top: 20px;
    }
  
    .toggle{
      margin-top: 12px;
      margin-left:20px;
      cursor: pointer;
      
    }
  
  .tg2{
    padding: 6px 4px 6px 9px;
    margin-top: 10px; 
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    color:#ecf0f5;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    display: block;
  }
  
  
  
    .logo{
      display: none;
    }
  .main{
    display: inline;
    
    }
  .box{
    margin-left: 20px;
    margin-right: 20px;
    padding: 20px;
  }
  .box7{
    margin-top: auto;
  }
  .grid-container{
    display: inline;
  }
  .hide{
  
    display: none;
  }
  
  .topbar{
    width:100%;
    height:auto;
  }
  
  
  }
  
  
  
  .order-card {
    color: #fff;
  }
  
  .bg-c-blue {
    background: linear-gradient(45deg,#4099ff,#73b4ff);
  }
  .bg-c-blue:hover{
    background: linear-gradient(45deg,#538bcc,#4683c9);
  }
  
  .bg-c-green {
    background: linear-gradient(45deg,#2ed8b6,#59e0c5);
  }
  .bg-c-green:hover {
    background: linear-gradient(45deg,#3eb49d,#429483);
  }
  
  
  .bg-c-yellow {
    background: linear-gradient(45deg,#FFB64D,#ffcb80);
  }
  .bg-c-yellow:hover {
    background: linear-gradient(45deg,#ce8f37,#a87226);
  }
  
  .bg-c-pink {
    background: linear-gradient(45deg,#FF5370,#ff869a);
  }
  
  .bg-c-pink:hover {
    background: linear-gradient(45deg,#c0334b,#cc3d55);
  }
  
  .bg-c-orange {
    background: linear-gradient(45deg,#cc7ef0,#f0966c);
  }
  .bg-c-orange:hover {
    background: linear-gradient(45deg,#8d2de7,#ec6f36);
  }
  
  
  .card {
    border-radius: 5px;
    -webkit-box-shadow: 0 1px 2.94px 0.06px rgba(4,26,55,0.16);
    box-shadow: 0 1px 2.94px 0.06px rgba(4,26,55,0.16);
    border: none;
    
  
  }
  
  .card .card-block {
    padding: 25px;
  }
  
  .order-card i {
    font-size: 10px;
  }
  
  
  /*extra*/
  
  
  .card5{
    padding: 125px;
  }
  .card2{
    padding:140px;
  }
  
  .card7{
    padding: 180px 50px;
    
  }
  
  .card1{
    padding: 50px;
  }
  .card6{
    padding: 68px;
  }
  
  



